@import url(https://fonts.googleapis.com/css2?family=Amatic+SC:wght@400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}
.shopping-cart {
  background-color: lightpink;
  padding: 2%;
}
.BlogContainer {
  max-width: 100%;
  background-color: lightpink;
  padding: 2% 0;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  /* height: 800px; */
}
.login-form form{
  width: 60%;
  margin: 1% auto;
  display: flex;
  justify-content: space-between;
  color: white;
}
.login {
  background-color: black;
  padding: 0.2%;
  /* color: white; */
}
.Hidden {
  width: 100%;
  background-color: black;
}
.login_btn {
  width: 10%;
  cursor: default;
  background-color: black;
  margin: 0;
  transition: 0.3s;
  text-align: left;
}
.login_btn:hover {
  color:grey;
  transition: 0.3s;
}
.Header {
  background-color: black;
  color: lightpink;
  text-align: center;
  padding: 1%;
  margin: 0;
}
.Header h1 {
  font-size: 5rem;
  font-family: 'Amatic SC', cursive;
  margin: 1%;
}
.Nav {
  display: flex;
  justify-content: center;
  font-size: 1rem;
}
.Cart {
  display: flex;
  flex-direction: row-reverse;
  margin-right: 5%;
}
.Link { 
  text-decoration: none;
  color: grey;
  font-size: 1.2rem;
  font-family: 'Playfair Display', serif;
  padding: 1%;
}
.Link:hover {
  color: lightpink;
  border: 1px dashed grey;
}
.CartLink {
  /* width: 10px; */
  display: inline-block;
}
.CartLink:hover {
  cursor: pointer;
}
.FooterStyles {
  display: flex;
  flex-direction: column;
  align-items: center;
  /* justify-content: space-evenly; */
  background-color: black;
  margin: 0;
  padding: 2%;
  text-align: center;
}
h4 {
  color: lightpink;
  padding: 1%;
  font-family: 'Gotu', sans-serif;
  margin: 0;
}
.Contact {
  width: 10%;
  margin: 0 auto;
}
.blog { 
  padding: 1%;
  margin: 2% auto;
  width: 65%;
}
.blog-header {
  text-align: center;
}
.blog-header h1 {
  margin: 2% 0;
}
.blog-header p {
  font-weight: bold;
}
hr {
  height: 2px;
  color: black;
  background-color: black;
  margin: 3% auto;
}
.blog-content {
  margin: 2% auto;
  line-height: 150%;
}
.date-icons {
  width: 55%;
  margin: 2% auto;
  display: flex;
  justify-content: space-between;
}
.material-icons {
  width: 20%;
  display: flex;
  justify-content: space-between;
}
.ShopContainer {
  background-color: lightpink;
  /* height: 800px; */
  padding: 5%;
}
.DeckContainer {
  background-color: black;
  max-width: 80%;
  margin: 0 auto;
}
.ProductRow {
  display: flex;
  justify-content: space-around;
  width: 100%;
}
.TopPagination {
  display: flex;
  justify-content: space-evenly;
  margin-left: 73%;
  width: 20%;
  color: lightpink;
}
.PaginationContainer {
  padding-top: 4%;
}

.Card {
  width: 55%;
  margin: 2% auto;
  display: flex;
  justify-content: space-between;
  padding: 2%;
  color: gray;
}
.Image {
  max-width: 45%;
}
img{
  width: 100%;
}
.Image:hover {
  -webkit-filter: blur(1px);
          filter: blur(1px);
}
.Content {
  width: 45%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.Content p {
  font-size: 1.3rem;
}
.Add {
  display: flex;
  flex-direction: row-reverse;
  margin-right: 5%;
  margin-bottom: 5%;
}
#purchase {
  border: 1px solid palevioletred;
  width: 30%;
  margin: 0 auto;
}
.AboutContainer {
  background-color: lightpink;
  padding: 5%;
}
.AboutContent {
  max-width: 35%;
  margin: 2% auto;
  padding: 2%;
  background-color: black;
}
#profile {
  width: 100%;
}
.Profile {
  background-color: lightpink;
  border: 2px solid;
  padding: 2%;
  color: black;
  margin-top: 2%;
}
.ContactBar {
  background-color: black;
  color: grey;
  width: 35%;
  margin: 0% auto;
  padding: 2%;
}
